*, *::before, *::after {
  box-sizing: border-box;
}

body {
  font-family: "Space Grotesk", sans-serif;
}

button {
  font: inherit;
  cursor: pointer;
  border: none;
  border-radius: 0.625rem;
  padding: 0.5rem 1rem;
  color: white;
  background-color: black;
}
button:hover {
  background-color: #2e2e2e;
}
button:active {
  background-color: #424242;
}
button:focus {
  background-color: black;
  color: #ffadd6;
}

p {
  text-transform: uppercase;
  font-weight: 700;
}